}
.title-font{
    font-family: $title-font;
}

h1, h2, h3, h4, h5, h6, p {
    padding: 0;
    margin: 0;
}
h1 {
    font-family: $title-font;
    font-size: 1.875rem;
    line-height: 3.5rem;
    @media (min-width: $grid-md-max){
        font-size: 2.375rem;
        line-height: 4.5rem;
    }
    margin: 2rem 0 2rem 0;
}

h2{
    font-family: $title-font;
    font-size: 1.375rem;
    line-height: 3rem;
    @media (min-width: $grid-md-max){
        font-size: 1.75rem;
        line-height: 3.75rem;
    }
    margin: 2rem 0 2rem 0;
}

h3 {
    font-family: $title-font;
    font-size: 1.25rem;
    line-height: 2.25rem;
    @media (min-width: $grid-md-max){
        font-size: 1.25rem;
        line-height: 3rem;
    }
    margin: 2rem 0 2rem 0;
}

h4 {
    font-family: $body-font;
    font-size: 1.125rem;
    font-weight: 700;
    line-height: 1.75rem;
    margin-bottom: 2rem;
}

.intro {
    font-family: $body-font;
    font-size: 1.25rem;
    font-weight: 400;
    line-height: 1.75rem;
    margin-bottom: 2rem;
}

p {
    max-width: 80ex;
    margin-bottom: 1rem;
}

a {
    color: $linkcolor;
    text-decoration: underline;
    &:hover {
        color: $gray-50;
        text-decoration: none;
    }
}

.giant-text {
	// this is deskstop size... needs mobile (used on typography page)
    font-size: 9.32rem;
	line-height: 9.8rem;
}
.small-text {
    font-family: $body-font;
    font-size: .75rem;
}

.italic {
    font-style: italic;
}

ul, ol {
